footer{
  width: 100%;
  height: 500px;
  position: relative;
  
  .bgWrap{
    width: 100%;
    height: 100%;
    &::after{
      content: '';
      display: block;
      clear: both;
    }

    .bgImg{
      width: 20%;
      height: 100%;
      float: left;
      background-color: #444;
      position: relative;
      img{
        width: 8%;
        position: absolute;
        bottom: 10px;
        right: 20px;
        object-fit: cover;
      }
    }
    .fCon{
      width: 80%;
      height: 100%;
      float: left;
      background-color: #fff;
      position: relative;
      p{
        width: 70%;
        position: absolute;
        bottom: 20px;
        font: 16px/1 $poinFont;
        color: #333;
        text-align: center;
      }
    }
  }
  
  .wrap{
    width: 80%;
    height: 100%;
    background-color: #000;
    position: absolute;
    top: -3vw;
    left: 50%;
    right: 50%;
    transform: translateX(-50%);
    padding: 50px;
    h3{
      font: 35px/1 'Fjalla One';
      color: #888;
      margin-bottom: 20px;
    }
    p{
      font: 20px/1 $poinFont;
      color: #777;
    }
    .navs{
      width: 100%;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      margin-top: 40px;
      .navWrap{
        padding-right: 100px;
        .nav{
          h2{
            font: 600 20px/1 $poinFont;
            color: #999;
            margin-bottom: 20px;
          }
          ul{
            li{
              font: 15px/1 $poinFont;
              color: #888;
              padding: 10px 0;
            }
          }
        }
      }
    }
    .pImg{
      position: absolute;
      top: -3vw;
      right: 2vw;
      width: 18%;
      img{
        width: 100%;
        object-fit: cover;
      }
    }
  }
}